import uniqid from 'uniqid';

export default class List {
    constructor(){
        this.items = [];
    }

    addItem (count, unit, ingredient) {
        const item =  {
            id: uniqid(),
            count,
            unit,
            ingredient
        }
        this.items.push(item);
        return item;
    }

    deleteItem(id) {
        const index = this.items.findIndex(el => el.id === id);
        //[2, 4, 8]
        //.splice(1, 1) krenut e na poziciji, vratiti traiti element na tom indexu,
        // jedan element obrisati jedan el. (drugi argument) i promijeniti orginalni array --> [2, 8]
        // .slice(1, 2) vraa traeni/e el, i ima poetnu i zavrnu poziciju kao parametre te ne mijenja poetni array!
        this.items.splice(index, 1);
    }

    updateCount(id, newCount) {
        this.items.find(el => el.id === id).count = newCount;
    }
}